<template>
    <div class="mb-5">
    <h4 class="mb-1">Account Analysis - USD</h4>

    <div class="rounded my-shadow table-60-vh"> 
      <b-table-simple no-border-collapse sticky-header bordered responsive striped small class="rounded mb-0">
        <b-thead>
          <b-tr>
            <b-th style="min-width: 250px">Name</b-th>
            <b-th>Close P/L</b-th>
            <b-th>Open P/L</b-th>
            <b-th>Net P/L</b-th>
            <b-th>Credit</b-th>
            <b-th>Current Equity</b-th>
            <b-th>Withdrawable</b-th>
            <b-th>Agent Commission</b-th>
            <b-th>Deposits</b-th>
            <b-th>Withdrawals</b-th>
            <b-th>Volume Traded</b-th>
            
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr class="text-right">
            <b-td class="text-center bg-black">Total</b-td>
            <b-td :class=" dataTotal.ClosedPL >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.ClosedPL, 0)}}</b-td>
            <b-td :class=" dataTotal.OpenPL >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.OpenPL, 0)}}</b-td>
            <b-td :class=" dataTotal.NetPL >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.NetPL, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.Credit, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.CurrentEquity, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.Withdrawable, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.AgentComission, 0)}}</b-td>
            <b-td :class=" dataTotal.Deposits >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.Deposits, 0)}}</b-td>
            <b-td :class=" dataTotal.Withdrawals >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.Withdrawals, 0)}}</b-td>
            <b-td :class=" dataTotal.VolTraded >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.VolTraded, 0)}}</b-td>
          </b-tr>
          <b-tr v-for="(item, index) in user_statistics" :key="index" class="text-right">
            <b-td class="text-left cursor-pointer" @click="viewAccountDetails(item)">{{item.properties.server}}-{{item.properties.login}}</b-td>
            <b-td :class="item.value.ClosedPL >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.ClosedPL, 0)}}</b-td>
            <b-td :class="item.value.OpenPL >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.OpenPL, 0)}}</b-td>
            <b-td :class="item.value.NetPL >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.NetPL, 0)}}</b-td>
            <b-td>{{formatMoney(item.value.CurrentCredit, 0)}}</b-td>
            <b-td>{{formatMoney(item.value.CurrentEquity, 0)}}</b-td>
            <b-td>{{formatMoney(item.value.Withdrawable, 0)}}</b-td>
            <b-td>{{formatMoney(item.value.AgentComission, 0)}}</b-td>
            <b-td :class="item.value.Deposits >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.Deposits, 0)}}</b-td>
            <b-td :class="item.value.Withdrawals >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.Withdrawals, 0)}}</b-td>
            <b-td :class="item.value.VolTraded >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.VolTraded, 0)}}</b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>

      <div class="py-1 d-flex">
        <div>
          <b-button size="sm" class="my-shadow ml-1 button-active" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Symbol </b-button>
          <b-button size="sm" class="my-shadow ml-1" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Company </b-button>
          <b-button size="sm" class="my-shadow ml-1" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Region </b-button>
          <b-button size="sm" class="my-shadow ml-1" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Team </b-button>
          <b-button size="sm" class="my-shadow ml-1" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Client </b-button>
          <b-button size="sm" class="my-shadow ml-1" v-ripple.400="'rgba(60, 60, 60, 0.6)'" variant="outline-dark" pill> Account </b-button>
        </div>
        <div class="ml-5 d-flex">
          <div class="my-auto mr-1">Timeframe: </div>
          <v-select class="select-size-sm width-160px" label="Time Frame" v-model="time_frame" :options="['This Month', 'Last Month', 'Overall']"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { BTableSimple, BThead, BTr, BTh, BTd, BTbody, BTfoot, BButton } from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import axios from '@axios'
import moment from "moment"
import { formatMoney } from '@core/utils/filter'
import vSelect from 'vue-select'

export default {
  components: {
    BTableSimple,
    BThead,
    BTr,
    BTh,
    BTd,
    BTbody,
    BTfoot,
    BButton,
    vSelect
  },
  directives: {
    Ripple,
  },
  mounted() {
    this.fetchAccountAnalysis();
  },
  data() {
    return {
      user_statistics: [],
      time_frame: 'This Month'
    }
  },
  computed: {
    dataTotal(){
      let label = ['ClosedPL', 'OpenPL', 'NetPL', 'CurrentCredit', 'CurrentEquity', 'Withdrawable', 'AgentComission', 'Deposits', 'Withdrawals', 'VolTraded']
      let result = {}
      label.map(field => result[field] = 0)
      this.user_statistics.map(item => {
        label.map(field => {
          if(item.value[field]) result[field] +=  item.value[field]
        })
      })
      return result
    }
    
  },
  methods: {
    moment,
    formatMoney,
    fetchAccountAnalysis() {
        let url = `${process.env.VUE_APP_API_URL}metric/CurrentUserStatistics/data`
        axios.get(url).then(res => {
            this.user_statistics = res.data
        }).catch(err => console.log(err))
    },
    viewAccountDetails(item) {
      this.$emit('viewAccoutDetails', item);
    },
    
  }
}
</script>
<style>
  .dark-layout .vs__dropdown-toggle {
    border-radius: 20px!important;
    box-shadow: 0px 0px 5px white;
  }

  .width-160px{
    width: 160px!important;
  }
</style>