<template>
  <div class="custom">
    <b-tabs vertical nav-wrapper-class="nav-vertical">
      <b-tab @click="$router.push('/b-book/market-exposure')">
        <template #title>
          <feather-icon icon="PieChartIcon"/>
        </template>
      </b-tab>
      
      <b-tab @click="$router.push('/b-book/user-stats')">
        <template #title>
          <feather-icon icon="UsersIcon"/>
        </template>
      </b-tab>
      
      <b-tab @click="$router.push('/b-book/company-p-l')">
        <template #title>
          <feather-icon icon="FilterIcon"/>
        </template>
      </b-tab>

      <b-tab active>
        <template #title>
          <feather-icon icon="BoxIcon"/>
        </template>
        <div style="max-width: calc(100vw - 148px)">
          <!-- Account Analysis -->
          <account-analysis 
            @viewAccoutDetails="viewAccoutDetails"
          />

          <!-- Account Detail, PL -->
          <account-details v-if="show_account_details"
            :current_item="current_item" 
          />

          <!-- 2 Charts -->
          <div class="row  m-0 justify-content-between" v-if="show_account_details">
            <div class="col-6 my-shadow pb-5 max-with-48" style="background: #18191A;">
              <h4 class="m-4">Account Profit History</h4>
              <ClientProfitHistoryCharts />
            </div>

             <div class="col-6 my-shadow pb-5 max-with-48" style="background: #18191A">
             <h4 class="m-4">Account Equity History</h4>
              <ClientEquityHistoryCharts />
            </div>
          </div>
          
        </div>
      </b-tab>

    </b-tabs>
  </div>
</template>

<script>
import { BTabs, BTab } from "bootstrap-vue";
import ClientProfitHistoryCharts from '../deepDriveCharts/ClientProfitHistoryCharts'
import ClientEquityHistoryCharts from '../deepDriveCharts/ClientEquityHistoryCharts'
import AccountAnalysis from './AccountAnalysis'
import AccountDetails from './AccountDetails'

export default {
  components: {
    BTabs,
    BTab,
    ClientProfitHistoryCharts,
    ClientEquityHistoryCharts,
    AccountAnalysis,
    AccountDetails
  },
 
  data(){
    return {
     current_item: {},
     show_account_details: false
    }
  },

  mounted() {
    // this.$store.commit('app/SET_DATA_LIVE_TIME', moment().format(' HH:mm:ss - DD/MM/YYYY'))
  },

  methods: {
    viewAccoutDetails(item) {
      this.current_item = item;
      this.show_account_details = true
    }
  }
};
</script>
<style>
  .max-with-48 {
    max-width: 48.7%;
  }
</style>